@import '../../colors.scss';

.title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 38px;
    z-index: 1;

    h3 {
        padding-left: 14px;
        position: relative;
        font-size: 28px;
        text-transform: lowercase;

        &::before {
            content: '';
            border-radius: 50%;
            position: absolute;
            left: 0;
            top: -2px;
            height: 32px;
            width: 32px;
            background-color: $MAGENTA;
            z-index: -1;
        }
    }

    a {
        margin-top: 2px;
        font-family: 'roc-grotesk-wide', sans-serif;
        font-weight: 600;
        font-size: 10px;
        line-height: 1;
        text-transform: uppercase;
        color: $MAGENTA;

        & > * {
            display: inline-block;
            vertical-align: middle;
        }

        img {
            margin: -2px 0 0 8px;
        }
    }
}
