@import '../../colors.scss';

.header {
    display: flex;
    justify-content: space-between;
    padding: 43px 16px 14px;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    transition: padding-top 0.3s, box-shadow 0.3s, background-color 0.3s;

    &.scrolled {
        padding: 9px 16px 7px;
        backdrop-filter: blur(1.6px);
        background-color: rgba($WHITE, 0.95);
    }
}
